.work-tabs {
  padding-top: 200px;
  padding-bottom: 270px;
  background-size: cover;
  background-position: center;
  @include media-breakpoint-down(sm) {
    padding-top: 83px;
    padding-bottom: 99px;
  }
  &__wrapper {
    @include flex-center;
  }
  .filter-box__tabs {
    margin-top: 85px;
  }
}
.work-content {
  background-color: #fff;
  padding-top: 80px;
  padding-bottom: 40px;
  margin-bottom: 800px;
  overflow: hidden;
  @include media-breakpoint-down(sm) {
    padding-top: 46px;
  }
  .rellax {
    opacity: 0.43;
    color: #e5e5e5;
    font-size: 300px;
    line-height: 400px;
    z-index: 0;
    position: absolute;
    display: none;
    margin-top: 650px;
    @include media-breakpoint-down(md) {
      margin-top: 390px;
    }
    @include media-breakpoint-down(sm) {
      margin-top: 444px;
    }
    @include dm-sans-medium;
  }
  .active {
    .rellax {
      display: block;
      top: 0;
      @include media-breakpoint-up(lg) {
        left: -90px;
      }
      @media (max-width: 1350px) {
        left: -75px;
      }
      @media (max-width: 1270px) {
        left: -50px;
      }
    }
  }
  &__content {
    position: relative;
    img {
      max-width: 100%;
    }
    .tab-pane {
      position: absolute;
      top: 0;
      background-color: #fff;
      min-height: 600px;
      &.active {
        position: relative;
      }
      h2 {
        color: #0d0d0d;
        font-size: 36px;
        text-align: center;
        margin-bottom: 100px;
        @include dm-sans-medium;
      }
    }
  }
  &__inner-container {
    position: relative;
    z-index: 1;
    h3 {
      color: #0d0d0d;
      font-size: 36px;
      @include dm-sans-medium;
    }
  }
  &__content-section {
    p {
      color: #0d0d0d;
      font-size: 21px;
      letter-spacing: 0.35px;
      line-height: 32px;
      margin-bottom: 44px;
      margin-top: 25px;
      @include dm-sans-reg;
      &:first-child {
        margin-top: 41px;
      }
      @include media-breakpoint-down(sm) {
        font-size: 18px;
      }
    }
  }
  &__inner-heading {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    margin-top: 104px;
    @include media-breakpoint-down(sm) {
      margin-top: 50px;
    }
    img {
      margin-right: 16px;
    }
  }
}
.how-we-work {
  h1 {
    margin-top: 10px;
    margin-bottom: 0;
    padding: 0 15px;
    text-align: center;
    color: #0d0d0d;
    font-size: 36px;
    line-height: 52px;
    @include dm-sans-medium;
    @include media-breakpoint-down(sm) {
      font-size: 24px;
      line-height: 28px;
    }
  }
  .left-position-text {
    span {
      @include media-breakpoint-down(sm) {
        padding-bottom: 0;
      }
    }
  }
  .work-content__inner-container {
    h2 {
      @media (max-width: 992px) {
        display: none;
        visibility: hidden;
      }
    }
  }
}



