.our-way {
  padding: 118px 24px 44px;
  position: relative;
  background-color: #f6f6f6;
  max-width: 315px;
  border-radius: 7px;
  transition: 0.2s;
  @include media-breakpoint-down(lg) {
    max-width: none;
    margin-bottom: 84px;
  }
  &__avatar {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    top: -44px;
    width: 128px;
    height: 128px;
    border-radius: 50%;
    z-index: 1;
    &:before {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      border-radius: 50%;
      background-color: rgba(0, 0, 0, 0.1);
      opacity: 1;
      z-index: 1;
      transition: 0.2s;
    }
    img {
      position: relative;
      width: 100%;
      height: 100%;
      z-index: -1;
    }
  }
  &__title {
    margin-bottom: 37px;
    h5 {
      font-size: 24px;
    }
  }
  &__description {
    margin-bottom: 51px;
    p {
      color: #404040;
    }
  }
  &__process {
    h6 {
      font-size: 18px;
      margin-bottom: 19px;
    }
  }
  &__label {
    display: inline-block;
    border-radius: 3px;
    border: 1px solid #d6d6d6;
    padding: 4px 9px;
    line-height: 21px;
    font-size: 14px;
    margin-bottom: 7px;
  }
  &:hover {
    background-color: #fff;
    .our-way__avatar:before {
      opacity: 0;
    }
  }
}



