.we-do {
  &__hero {
    background-color: $gray-ultra-light;
    padding-top: 211px;
    @include media-breakpoint-down(md) {
      padding-top: 150px;
      padding-bottom: 59px;
    }
    @include media-breakpoint-down(xs) {
      padding-top: 120px;
    }
    h3 {
      color: $body-text;
      @include dm-sans-medium;
      font-size: 36px;
      font-weight: 500;
      line-height: 52px;
      @include media-breakpoint-down(sm) {
        line-height: 28px;
      }
    }
    p {
      color: $body-text;
      line-height: 26px;
      font-size: 18px;
    }
    span {
      color: #fd3034;
    }
    .col-12 {
      @include media-breakpoint-down(md) {
        order: -1;
      }
    }
    &__img {
      margin-top: 149px;
      min-height: 235px;
      @include media-breakpoint-down(md) {
        margin-top: 74px;
        margin-bottom: 30px;
      }
      @include media-breakpoint-down(sm) {
        margin-top: 33px;
      }
    }
    &__title {
      @include media-breakpoint-down(md) {
        order: -2;
      }
    }
  }
}
.capabilities {
  &__section {
    padding: 91px 0 154px;
    background-color: #fff;
    @include media-breakpoint-down(md) {
      padding-bottom: 64px;
    }
    @include media-breakpoint-down(sm) {
      padding-top: 35px;
    }
  }
  &__head {
    p {
      font-size: 22px;
      line-height: 32px;
      margin-bottom: 83px;
      @include media-breakpoint-down(sm) {
        font-size: 19px;
        line-height: 25px;
        margin-bottom: 40px;
      }
    }
  }
  &__content {
    .capability-tile {
      margin-bottom: 60px;
      @include media-breakpoint-down(lg) {
        margin-bottom: 20px;
      }
      @include media-breakpoint-down(md) {
        margin-bottom: 10px;
      }
      @include media-breakpoint-down(md) {
        margin-bottom: 24px;
      }
      &:nth-child(odd) {
        margin-right: 40px;
        @include media-breakpoint-down(lg) {
          margin-right: 20px;
        }
        @include media-breakpoint-down(md) {
          margin-right: 10px;
        }
        @include media-breakpoint-down(sm) {
          margin-right: 0;
        }
      }
    }
  }
}
.our-way {
  background-color: #fff;
  &__section {
    padding-top: 91px;
    padding-bottom: 157px;
    background-color: #f1f1f1;
    @include media-breakpoint-down(lg) {
      padding-bottom: 0;
    }
    @include media-breakpoint-down(sm) {
      padding-top: 40px;
    }
    .capabilities__head {
      margin-bottom: 89px;
      @include media-breakpoint-down(lg) {
        margin-bottom: 45px;
      }
    }
    @include media-breakpoint-up(lg) {
      .our-way {
        &:first-child {
          margin-right: 5px;
        }
        &:last-child {
          margin-left: 5px;
        }
      }
    }
  }
  &__content {
    @include media-breakpoint-down(md) {
      flex-direction: column;
    }
  }
}
.what-we-do {
  .left-position-text {
    span {
      @include media-breakpoint-down(sm) {
        padding-bottom: 0;
      }
    }
  }
}



