  cursor: pointer;
  color: #fff;
}

.red-btn:hover:before {
  transform: translateX(500px) skewX(-15deg);
  opacity: .6;
  transition: .5s;
}

.red-btn:hover:after {
  transform: translateX(500px) skewX(-15deg);
