.portfolio-info {
  padding-top: 180px;
  padding-bottom: 60px;
  background-color: #f1f1f1;
  &__text {
    h3 {
      color: #0d0d0d;
      font-size: 36px;
      font-weight: 500;
      line-height: 52px;
      display: block;
      margin-top: 18px;
      margin-bottom: 32px;
      @include dm-sans-medium;
      span {
        color: #fd3034;
      }
    }
  }
  &__image {
    position: relative;
    &:before {
      content: url("images/portfolio/bg.svg");
      position: absolute;
      z-index: -1;
      left: -265px;
      top: 0;
      overflow: hidden;
      max-height: 412px;
    }
    img {
      width: 100%;
    }
  }
}
.filter-box {
  position: relative;
  z-index: 10;
  padding-top: 120px;
  &__show {
    p {
      color: #0d0d0d;
      @include dm-sans-medium;
      font-size: 24px;
    }
  }
  &__item {
    position: relative;
    color: #0d0d0d;
    padding-left: 65px;
    @include dm-sans-medium;
    font-size: 24px;
    &:before {
      color: #fa333c;
      position: absolute;
      font-size: 105px;
      height: 24px;
      line-height: 27px;
      left: 24px;
      top: 1px;
      content: "";
    }
  }
  &__tabs {
    margin-top: 60px;
    .nav-tabs {
      @include media-breakpoint-down(sm) {
        justify-content: space-around;
      }
    }
    .nav-link {
      @media (max-width: 600px) {
        margin: 0;
      }
    }
    ul {
      display: flex;
      margin: 0;
      list-style: none;
      padding: 0;
      flex-direction: row;
      flex-wrap: wrap;
      justify-content: flex-start;
      li {
        margin-bottom: 25px;
        &:last-of-type {
          a {
            margin-right: 0;
          }
        }
        a {
          min-width: 180px;
          background: #f1f1f1;
          position: relative;
          color: #0d0d0d;
          font-size: 21px;
          font-weight: 500;
          border: 2px solid #d6d6d6;
          padding: 22px 37px 24px 56px;
          margin-right: 30px;
          transition: 0.3s;
          display: inline-block;
          @include dm-sans-medium;
          &:before {
            color: #d8d8d8;
            position: absolute;
            font-size: 105px;
            height: 24px;
            line-height: 27px;
            left: 10px;
            top: -3px;
            content: "\2022";
            bottom: 0;
            margin: auto;
            transition: 0.3s;
          }
          &.active {
            background-color: #d5d2d2;
            color: #fa333c;
            &:before {
              color: #fa333c;
            }
          }
          &:hover {
            background-color: #d5d2d2;
            color: #fa333c;
            &:before {
              color: #fa333c;
            }
          }
        }
      }
    }
  }
  .body-tabs {
    width: 100%;
    overflow-x: hidden;
    &__content {
      outline: none;
      position: relative;
      padding-bottom: 240px;
      .tab-pane {
        &.fade {
          display: none;
          visibility: hidden;
        }
        &.active {
          visibility: visible;
          display: block;
          position: relative !important;
          top: 0;
        }
      }
    }
  }
}
.portfolio {
  position: relative;
  &__inner {
    transition: background-color 0.5s;
    background-color: white;
  }
}



