body {
  background-color: #f1f1f1;
  font-family: "DM-Sans", sans-serif;
}

.list {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

.flex-center {
  justify-content: center;
  align-items: center;
}

a {
  &:hover {
    text-decoration: none;
  }
}

.red-span {
  color: $red;
}

.block-head {
  display: flex;
  justify-content: space-between;
  p {
    font-size: 21px !important;
    color: #0d0d0d !important;
  }
}

.red-btn {
  padding: 0 32px;
  height: 68px;
  width: fit-content;
  background-color: $red;
  color: $white;
  font-size: 18px;
  font-weight: 700;
  letter-spacing: 1.29px;
  position: relative;
  display: flex;
  @extend .flex-center;
  transition: 0.6s;
  overflow: hidden;
  &:before {
    content: "";
    display: block;
    position: absolute;
    background: rgba(255, 255, 255, 0.5);
    width: 60px;
    height: 100%;
    left: 0;
    top: 0;
    opacity: 0.2;
    filter: blur(30px);
    transform: translateX(-100px)  skewX(-15deg);
  }
  &:after {
    content: "";
    display: block;
    position: absolute;
    background: rgba(255, 255, 255, 0.2);
    width: 30px;
    height: 100%;
    left: 30px;
    top: 0;
    opacity: 0;
    filter: blur(5px);
    transform: translateX(-100px) skewX(-15deg);
  }
  &:hover {
    background: #e02a33;
    cursor: pointer;
    color: $white;
    &:before {
      transform: translateX(500px)  skewX(-15deg);
      opacity: 0.6;
      transition: 0.5s;
    }
    &:after {
      transform: translateX(500px) skewX(-15deg);
      opacity: 1;
      transition: 0.5s;
    }
  }
}

.red-border-btn {
  width: 171px;
  height: 48px;
  border: 1px solid $red;
  display: flex;
  @extend .flex-center;
  color: $red;
  font-size: 14px;
  font-weight: 700;
  letter-spacing: 1px;
  position: relative;
  overflow: hidden;
  z-index: 2;
  transition: 0.3s;
  &:before {
    content: "";
    position: absolute;
    top: 0;
    right: calc(100% + 10px);
    height: 100%;
    background: $red;
    width: calc(100% + 50px);
    transform: skewX(-20deg);
    transition: 0.3s;
    z-index: -1;
  }
  &:hover {
    color: $white;
    &:before {
      right: -20px;
    }
  }
}

.left-position-text {
  line-height: 40px;
  color: $gray;
  font-size: 36px;
  font-weight: 500;
  transition: 0.3s;
  @include media-breakpoint-up(lg) {
    position: absolute;
    top: -85px;
    left: -80px;
    height: calc(100% + 40px);
    width: 40px;
    margin: 0;
  }
  @media (max-width: 1350px) {
    left: -40px;
  }
  @media (max-width: 1270px) {
    left: 13px;
  }
  &__container {
    @media (min-width: 992px) and (max-width: 1270px) {
      padding-left: 65px;
      padding-right: 50px;
    }
  }
  &.white {
    color: $white;
  }
  span {
    white-space: nowrap;
    z-index: 1;
    @include media-breakpoint-up(lg) {
      writing-mode: vertical-rl;
      transform: rotate(180deg);
      position: sticky;
      padding-bottom: 85px;
      top: -70px;
    }
    @media (max-width: 1270px) {
      padding-bottom: 90px;
      top: 0;
    }
    @include media-breakpoint-down(md) {
      display: block;
      margin-bottom: 35px;
    }
    &::first-letter {
      color: $red;
    }
  }
}

.scrolling {
  width: 100%;
  height: 353px;
  position: absolute;
  left: 0;
  top: 15%;
  &__text {
    opacity: 0.43;
    font-size: 300px !important;
    font-weight: 500 !important;
    color: #fff !important;
    line-height: normal !important;
    white-space: nowrap;
    transition: 0.5s;
  }
  @include media-breakpoint-down(md) {
    display: none;
  }
}

.red-first-letter::first-letter {
  color: #fa333c;
}

.particle-background {
  display: none;
  @media screen and (min-width: 990px) {
    display: block;
    position: absolute;
    z-index: 0;
    background-color: #0e0e0e;
    bottom: 0;
    top: 0;
    left: 0;
    right: 0;
    width: 100%;
    height: 100%;
    background-image: url("images/footer-bg.png");
    background-size: cover;
    background-position: center;
    canvas {
      bottom: 0;
      height: 800px !important;
      position: relative;
    }
  }
}

.page-bottom {
  position: relative;
}

@mixin btn-anime($time, $cubic) {
  transition: all $time $cubic;
}
.btn {
  font-size: 16px;
  transform: translate(0, 0);
  overflow: hidden;
  cursor: pointer;
  text-decoration: none;
  padding: 17px 55px;
  min-width: 192px;
  border-radius: 0;
  @include btn-anime(0.6s, (cubic-bezier(0.55, 0, 0.1, 1)));
  @include dm-sans-bold;
  &__white {
    color: #000;
    border: #fff 2px solid;
    background-color: #fff;
    box-shadow: 0 0 0 #fff;
  }
  &__black {
    color: #fff;
    border: #2e2e2e 2px solid;
    background-color: #2e2e2e;
    box-shadow: 0 0 0 #2e2e2e;
  }
  &__red {
    color: #fff;
    border: transparent 2px solid;
    background-color: #fa333c;
    box-shadow: 0 0 0 #fa333c;
  }
  &__hover-white {
    &:hover {
      color: #2e2e2e;
      //text-shadow: 0 8px 6px rgba(120, 120, 120, 0.9);
      box-shadow: 8px 8px 0 #bfbfbf, -8px -8px 0 #bfbfbf;
      text-decoration: none;
      .btn__color-one {
        background-color: #4d4d4d;
      }
      .btn__color-two {
        background-color: #707070;
      }
      .btn__color-three {
        background-color: #bfbfbf;
      }
      .btn__color-four {
        background-color: #fff;
      }
    }
  }
  &__hover-grey {
    &:hover {
      color: #2e2e2e;
      box-shadow: 8px 8px 0 #bfbfbf, -8px -8px 0 #bfbfbf;
      text-decoration: none;
      .btn__color-one {
        background-color: #4d4d4d;
      }
      .btn__color-two {
        background-color: #707070;
      }
      .btn__color-three {
        background-color: #bfbfbf;
      }
      .btn__color-four {
        background-color: #fff;
      }
    }
  }
  &__hover-black {
    &:hover {
      color: #fff;
      box-shadow: 8px 8px 0 #6e6e6e, -8px -8px 0 #6e6e6e;
      text-decoration: none;
      .btn__color-one {
        background-color: #9c9c9c;
      }
      .btn__color-two {
        background-color: #6e6e6e;
      }
      .btn__color-three {
        background-color: #232323;
      }
      .btn__color-four {
        background-color: #000;
      }
    }
  }
  &__hover-red {
    &:hover {
      color: #fff;
      box-shadow: 8px 8px 0 #ff9898, -8px -8px 0 #ff9898;
      text-decoration: none;
      .btn__color-one {
        background-color: #ff9898;
      }
      .btn__color-two {
        background-color: #ff9898;
      }
      .btn__color-three {
        background-color: #fd6060;
      }
      .btn__color-four {
        background-color: #fa333c;
      }
    }
  }
  &:hover .btn__color-one {
    opacity: 1;
    transform: translate3d(0, 0, 0);
  }
  &:hover .btn__color-two {
    transform: translate3d(0, 0, 0);
  }
  &:hover .btn__color-three {
    transform: translate3d(0, 0, 0);
  }
  &:hover .btn__color-four {
    transform: translate3d(0, 0, 0);
  }
  &__inner {
    width: 100%;
    height: 100%;
    position: absolute;
    background: transparent;
    top: 0;
    left: 0;
    transform: translate3d(0, 90px, 0);
  }
  .btn__color-one {
    @include btn-anime(0.3s, (cubic-bezier(0.55, 0, 0.1, 1)));
    z-index: -3;
  }
  .btn__color-two {
    @include btn-anime(0.5s, (cubic-bezier(0.55, 0, 0.1, 1)));
    z-index: -3;
  }
  .btn__color-three {
    @include btn-anime(0.7s, (cubic-bezier(0.55, 0, 0.1,1)));
    z-index: -3;
  }
  .btn__color-four {
    @include btn-anime(0.9s, (cubic-bezier(0.55, 0, 0.1, 1)));
    z-index: -3;
  }
}
.margin-footer {
  margin-bottom: 810px;
  @media (max-height: 800px) {
    margin-bottom: 750px;
  }
  @media (max-height: 700px) {
    margin-bottom: 700px;
  }
}



