header {
  padding-top: 12px;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 9;
  .header {
    height: 75px;
    width: fit-content;
    overflow: hidden;
    padding-bottom: 20px;
    @include media-breakpoint-down(md) {
      position: fixed;
      top: 12px;
      left: 16px;
    }
    .logo {
      background-color: $red;
      flex: 0 0 55px;
      width: 55px;
      position: relative;
      z-index: 2;
    }
    &__menu {
      position: relative;
      z-index: 1;
      width: 110px;
      transition: 0.3s;
      transition-delay: 0.6s;
      &__icon {
        flex: 0 0 55px;
        width: 55px;
        height: 55px;
        flex-direction: column;
        position: relative;
        transform: translateX(0);
        transition: 0.3s;
        transition-delay: 0.3s;
        background: $white;
        left: 0;
        top: 0;
        span {
          display: block;
          width: 27px;
          height: 3px;
          background-color: #0d0d0d;
          margin-bottom: 7px;
          transition: 0.3s;
          transition-delay: 0.6s;
          &:first-child,
          &:last-child {
            margin-left: 2px;
          }
          &:nth-child(2) {
            margin-right: 2px;
          }
          &:last-child {
            margin-bottom: 0;
          }
        }
        &:hover {
          span {
            transition-delay: 0s;
            &:first-child,
            &:last-child {
              margin-left: -2px;
            }
            &:nth-child(2) {
              margin-right: -2px;
            }
          }
        }
      }
      @include media-breakpoint-up(lg) {
        &:hover {
          width: 100%;
          transition-delay: 0s;
          .menu-main {
            opacity: 1;
            transform: translateX(0) !important;
            transition-delay: 0.6s !important;
          }
          .header__menu__icon {
            transform: translateX(-100%);
            box-shadow: none;
            transition-delay: 0.3s;
          }
        }
      }
    }
    #menu-main {
      opacity: 1;
      margin-left: -55px;
      position: relative;
      transform: translateX(-100%);
      transition: 0.3s;
      transition-delay: 0s;
      background-color: $white;
      height: 55px;
      overflow: hidden;
      li {
        padding: 0 30px;
        a {
          color: $gray;
          font-size: 16px;
          font-weight: 500;
          letter-spacing: 0.3px;
          transition: 0.3s;
          span {
            position: relative;
            overflow: hidden;
            transition: 0.6s;
            min-width: 5px;
            .out {
              display: inline-flex;
            }
            .in {
              position: absolute;
              left: 0;
              opacity: 0;
              transform: translateX(100%);
              color: #e02a33;
            }
          }
          &:hover {
            span {
              .out {
                opacity: 0;
                transform: translateX(-100%);
              }
              .in {
                opacity: 1;
                transform: translateX(0);
              }
            }
          }
        }
      }
    }
  }
  .menu-main--mob {
    position: fixed;
    top: 0;
    left: -100%;
    background-color: rgba(0, 0, 0, 0.2);
    height: 100%;
    width: 100%;
    z-index: 2000;
    &__content {
      padding-top: 32px;
      background-color: #fff;
      transform: translateX(-100%);
      max-width: 304px;
      height: 100%;
      overflow-y: auto;
      transition: 0.3s;
      img {
        margin-left: 20px;
      }
      @media (max-width: 359px) {
        max-width: 280px;
      }
    }
    &.show {
      left: 0;
      .menu-main--mob__content {
        transform: translateX(0);
      }
    }
    &__list {
      &:first-of-type {
        margin-top: 32px;
      }
      a {
        display: block;
        padding: 16px 0 19px 20px;
        width: 100%;
        color: #0d0d0d;
        font-size: 18px;
        font-weight: 500;
        letter-spacing: 0.34px;
        transition: 0.2s;
      }
      li {
        min-width: 50%;
        &.active {
          a {
            color: #fff;
            background-color: #ff4141;
          }
        }
      }
      &.extra {
        a {
          font-size: 16px;
          color: #000;
        }
      }
    }
    hr {
      border: 0.5px solid #f1f1f1;
    }
  }
  @include media-breakpoint-up(lg) {
    &.opened {
      .header__menu__icon span {
        transition-delay: 0s;
        &:first-child,
        &:last-child {
          margin-left: -2px;
        }
        &:nth-child(2) {
          margin-right: -2px;
        }
      }
      .header__menu {
        width: 100%;
        transition-delay: 0s;
        .menu-main {
          opacity: 1;
          transform: translateX(0) !important;
          transition-delay: 0.6s !important;
        }
        .header__menu__icon {
          transform: translateX(-100%);
          box-shadow: none;
          transition-delay: 0.3s;
        }
      }
    }
  }
}



