.team {
  background-color: #fff;
}
.polygon-chart {
  @include media-breakpoint-down(md) {
    margin-right: 20px;
  }
}
.team-member {
  padding-top: 125px;
  background-color: #f1f1f1;
  .left-position-text {
    span {
      color: #0d0d0d;
      @include media-breakpoint-down(md) {
        margin-bottom: 0;
        padding-bottom: 65px;
      }
      @include media-breakpoint-down(sm) {
        padding-bottom: 45px;
      }
    }
  }
}
.team-hero {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  @include media-breakpoint-down(sm) {
    flex-direction: column;
  }
  &__left {
    img {
      max-width: 100%;
    }
  }
  &__right {
    width: 100%;
    h5 {
      color: rgba(0, 0, 0, 0.48);
      font-family: "DM Sans - Medium", sans-serif;
      font-size: 18px;
      font-weight: 500;
      margin-bottom: 0;
    }
  }
  &__heading {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-content: center;
    padding: 35px 32px 27px 32px;
    background-color: rgba(255, 255, 255, 0.48);
    h2 {
      margin-bottom: 21px;
    }
    span {
      margin-bottom: 0;
    }
    @include media-breakpoint-down(lg) {
      padding: 35px 25px 27px 20px;
    }
    @include media-breakpoint-down(md) {
      flex-direction: column;
    }
  }
  &__title {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    h2 {
      color: #0d0d0d;
      font-family: "DM Sans - Medium", sans-serif;
      font-size: 36px;
      font-weight: 500;
    }
    @include media-breakpoint-down(md) {
      margin: auto;
      padding-bottom: 15px;
    }
  }
  &__duo-links {
    display: flex;
    flex-direction: row;
    align-items: center;
    .btn {
      margin-right: 25px;
      padding: 17px 45px;
      @include media-breakpoint-down(md) {
        margin: auto;
      }
      @media (max-width: 576px) {
        &:first-child {
          margin-bottom: 15px;
        }
      }
    }
    @include media-breakpoint-down(lg) {
      padding-left: 10px;
    }
    @media (max-width: 576px) {
      flex-direction: column;
      padding-left: 0;
    }
  }
  &__skills {
    background-color: rgba(255, 255, 255, 0.48);
    padding: 24px 32px 14px 32px;
    margin-top: 20px;
    h5 {
      margin-bottom: 25px;
      @include media-breakpoint-down(sm) {
        display: none;
        visibility: hidden;
      }
    }
  }
  .\.hidden-sm {
    @include media-breakpoint-down(sm) {
      //display: none;
    }
  }
}
.portret {
  @include media-breakpoint-down(sm) {
    max-height: fit-content;
    margin: auto auto 20px;
  }
}
.skills {
  display: inline-flex;
  flex-direction: row;
  word-wrap: break-spaces;
  overflow: hidden;
  flex-wrap: wrap;
  &__content {
    height: 32px;
    @include flex-center;
    padding: 7px 16px;
    border: 1px solid #fff;
    border-radius: 24px;
    margin: 0 16px 16px 0;
    background-color: #fff;
    cursor: pointer;
    transition: 1s;
    opacity: 1;
    @include media-breakpoint-down(sm) {
      border: 1px solid red;
    }
    p {
      color: #0d0d0d;
      @include dm-sans-medium;
    }
  }
  &__hovered {
    opacity: 0;
  }
}
.expierence {
  margin-top: 40px;
  @include media-breakpoint-down(sm) {
    margin-top: 0;
  }
  .\.hidden-sm {
    @include media-breakpoint-down(sm) {
      display: none;
    }
  }
  .team-blocks {
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    margin-top: 24px;
    @media (max-width: 767px) {
      flex-direction: column;
    }
    &__block {
      border: 2px solid #d6d6d6;
      padding: 30px 61px 49px 40px;
      margin-bottom: 32px;
      &:last-child {
        margin-bottom: 152px;
        @include media-breakpoint-down(sm) {
          margin-bottom: 40px;
        }
      }
      @media (max-width: 767px) {
        margin-bottom: 24px;
        padding: 40px 42px 49px 32px;
      }
      &--header {
        display: flex;
        align-items: center;
      }
      h3 {
        color: #0d0d0d;
        @include dm-sans-medium;
        font-size: 24px;
        display: inline-block;
        &:first-letter {
          color: #fa333c;
        }
      }
      &--ball {
        width: 34px;
        height: 34px;
        border-radius: 50%;
        background-color: #d8d8d8;
        display: inline-block;
        position: relative;
        margin-right: 16px;
        &::before {
          content: "";
          position: absolute;
          width: 20px;
          height: 1px;
          background-color: #c4cdd5;
          transform: rotate(45deg);
          top: 16px;
          left: 8px;
        }
      }
      p {
        color: #0d0d0d;
        @include dm-sans-reg;
        font-size: 18px;
      }
      &--text {
        margin-top: 30px;
        color: #404040;
        margin-bottom: 45px;
      }
      &--links {
        margin-top: 18px;
        display: flex;
        flex-wrap: wrap;
        a {
          color: #0d0d0d;
          @include dm-sans-medium;
          margin-right: 5px;
          transition: 0.2s;
          &:hover {
            color: #fa333c;
            text-decoration: underline;
          }
        }
      }
    }
  }
}
.team-page {
  &__content {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  &__headers {
    display: flex;
    h2 {
      font-size: 36px;
      font-family: "DM Sans", sans-serif;
      &:first-child {
        margin-right: 10px;
      }
      &::first-letter {
        color: #fa333c;
      }
    }
  }
  &__desk {
    opacity: 0.72;
    color: #0d0d0d;
    font-size: 18px;
    @include dm-sans-reg;
  }
  &__duo-links {
    display: flex;
    max-width: 358px;
    justify-content: space-between;
    width: 100%;
    margin-top: 43px;
    @media (max-width: 767px) {
      flex-direction: column;
    }
    &--btn {
      color: #fa333c;
      @include dm-sans-bold;
      letter-spacing: 1px;
      max-width: 171px;
      width: 100%;
      height: 48px;
      border: 1px solid #fa333c;
      @include flex-center;
      transition: 0.2s;
      @include dm-sans-bold;
      @media (max-width: 767px) {
        max-width: 328px;
      }
      &:hover {
        background-color: #fa333c;
        color: #fff;
      }
      &:last-child {
        color: #fff;
        background-color: #fa333c;
        @media (max-width: 767px) {
          margin-top: 25px;
        }
        &:hover {
          color: #fa333c;
          background-color: transparent;
        }
      }
    }
  }
}



