footer {
  background-color: $gray2;
  background-size: cover;
  background-position: center;
  position: fixed;
  margin: 0;
  overflow: hidden;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: -1;
  height: 810px;
  @include dm-sans-medium;
  & * {
    position: relative;
    z-index: 2;
  }
  .logo {
    margin-bottom: 35px;
    display: block;
    @include media-breakpoint-down(xs) {
      margin-bottom: 25px;
    }
  }
  li {
    padding: 0 5px;
    a {
      color: $white !important;
      transition: 0.3s;
      @include dm-sans-medium;
      span {
        position: relative;
        overflow: hidden;
        transition: 0.6s;
        min-width: 5px;
        .out {
          display: inline-flex;
        }
        .in {
          position: absolute;
          left: 0;
          opacity: 0;
          transform: translateX(100%);
          color: #e02a33;
        }
      }
      &:hover {
        span {
          .out {
            opacity: 0;
            transform: translateX(-100%);
          }
          .in {
            opacity: 1;
            transform: translateX(0);
          }
        }
      }
    }
    &:last-child {
      margin-bottom: 0 !important;
    }
  }
  .first {
    @include media-breakpoint-down(sm) {
      margin-bottom: 70px;
    }
    @include media-breakpoint-down(xs) {
      margin-bottom: 25px;
    }
    li {
      margin-bottom: 12px;
      padding-left: 30px;
      position: relative;
      @include dm-sans-medium;
      &:before {
        content: "";
        display: block;
        width: 14px;
        height: 14px;
        background-color: $red;
        border-radius: 100%;
        position: absolute;
        left: 0;
        top: 5px;
      }
      a {
        font-size: 14px;
        font-weight: 400;
        letter-spacing: 0.26px;
      }
    }
  }
  .other {
    li {
      margin-bottom: 20px;
      @include dm-sans-medium;
      @include media-breakpoint-down(xs) {
        margin-bottom: 7px;
      }
      a {
        font-size: 16px;
        font-weight: 500;
        letter-spacing: 0.3px;
      }
    }
  }
  .top {
    width: 64px;
    height: 64px;
    background-color: $white;
    border-radius: 100%;
  }
  .copyright {
    opacity: 0.24;
    color: $white;
    font-size: 14px;
    font-weight: 400;
    letter-spacing: 0.26px;
    margin-top: 45px;
    margin-bottom: 45px;
    @include media-breakpoint-down(xs) {
      margin-bottom: 5px;
      margin-top: 25px;
    }
    @media (max-height: 700px) {
      margin-top: 15px;
      margin-bottom: 15px;
    }
    br {
      @include media-breakpoint-up(sm) {
        display: none;
      }
    }
  }
  .to-top {
    z-index: 1;
    transition: 0.2s;
    &.show {
      opacity: 1;
    }
    @include media-breakpoint-down(md) {
      position: fixed;
      right: 48px;
      bottom: 48px;
      opacity: 0;
    }
    @media (max-width: 360px) {
      right: 24px;
      bottom: 24px;
    }
  }
}
.footer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  &__inner {
    position: relative;
    margin-top: auto;
  }
}



