.index {
  overflow-x: hidden;
}
.home {
  position: relative;
  background-color: #f1f1f1;
  z-index: 2;
  p {
    color: $white;
    font-size: 18px;
    font-weight: 400;
    line-height: 26px;
  }
  &__banner {
    height: 100vh;
    background-size: cover;
    background-position: center;
    overflow: hidden;
    position: relative;
    z-index: 5;
    background-color: #fff;
    @media (max-width: 991px) {
      height: auto;
      min-height: 852px;
    }
    &:before {
      content: "";
      background-color: #f1f1f1;
      opacity: 0.5;
      z-index: 1;
      @extend .absolute-fill;
    }
    &__video {
      position: absolute;
      top: 50%;
      left: 50%;
      min-width: 100%;
      min-height: 100%;
      width: auto;
      height: auto;
      transform: translate(-50%, -50%);
      z-index: -1;
    }
  }
  &__projects {
    padding: 85px 0 130px;
    position: relative;
    @include media-breakpoint-down(md) {
      padding-bottom: 65px;
    }
    @include media-breakpoint-down(sm) {
      padding-bottom: 135px;
    }
    &__list {
      margin-top: 280px;
      @include media-breakpoint-down(md) {
        margin-top: 57px;
      }
      &__item {
        display: block;
        flex: 0 0 33.3333%;
        transform: perspective(500px) rotateY(-10deg);
        margin-left: -11.55%;
        box-shadow: 3px 3px 23px rgba(111, 111, 111, 0.49);
        position: relative;
        transition: 0.3s;
        &:hover {
          z-index: 5 !important;
          transform: perspective(0) rotateY(0deg);
        }
        &:first-child {
          margin-left: 0;
          z-index: 4;
        }
        &:nth-child(2) {
          z-index: 3;
        }
        &:nth-child(3) {
          z-index: 2;
        }
        &:nth-child(4) {
          z-index: 1;
        }
        .portfolio-image {
          max-width: 100%;
        }
      }
    }
    .block-head .right {
      @include media-breakpoint-only(md) {
        transform: translateY(-165%);
      }
      @include media-breakpoint-down(sm) {
        position: absolute;
        bottom: -104px;
        left: 50%;
        transform: translateX(-50%);
        z-index: 1;
        a {
          background-color: #fa333c;
          color: #fff;
        }
      }
      @include media-breakpoint-down(xs) {
        width: calc(100% - 32px);
        a {
          width: 100%;
          height: 68px;
          text-align: center;
          font-size: 18px;
        }
      }
    }
  }
  &__use {
    padding: 80px 0 200px;
    position: relative;
    overflow: hidden;
    @include media-breakpoint-down(md) {
      padding-bottom: 85px;
    }
    @include media-breakpoint-down(sm) {
      padding-bottom: 150px;
    }
    .scrolling__text {
      position: absolute;
      transform: translateX(100vw);
      opacity: 0;
    }
    &__specifies {
      display: block;
      padding-bottom: 30px;
      border-radius: 15px;
      margin-top: 150px;
      background: transparent;
      transition: 0.3s;
      @include media-breakpoint-down(md) {
        margin-top: 20px;
      }
      &__icons {
        height: 340px;
        display: block;
        position: relative;
        @include media-breakpoint-down(sm) {
          height: 290px;
        }
        .icon {
          width: 69px;
          height: 69px;
          border-radius: 100%;
          background-color: rgba(255, 255, 255, 0.43);
          position: absolute;
          transition: 3.5s;
          transition-timing-function: ease-in-out;
          &:first-child {
            left: 20%;
            top: 10%;
          }
          &:nth-child(2) {
            right: 23%;
            top: 13%;
          }
          &:nth-child(3) {
            left: 23%;
            top: 60%;
          }
          &:nth-child(4) {
            top: 37%;
            right: 40%;
          }
          &:last-child {
            right: 20%;
            bottom: 10%;
          }
        }
      }
      .title {
        display: block;
        text-align: center;
        opacity: 0.72;
        color: $gray;
        font-size: 20px;
        font-weight: 500;
        transition: 0.3s;
      }
      &:hover {
        background-color: rgba(250, 250, 250, 0.46);
        .title {
          color: $red;
          opacity: 1;
        }
      }
    }
    .block-head .right {
      @include media-breakpoint-only(md) {
        transform: translateY(-165%);
      }
      @include media-breakpoint-down(sm) {
        position: absolute;
        bottom: -90px;
        left: 50%;
        transform: translateX(-50%);
        z-index: 1;
        a {
          background-color: #fa333c;
          color: #fff;
        }
      }
      @include media-breakpoint-down(xs) {
        width: calc(100% - 32px);
        a {
          width: 100%;
          height: 68px;
          text-align: center;
          font-size: 18px;
        }
      }
    }
  }
  &__about {
    background: $gray2;
    padding: 85px 0 165px;
    position: relative;
    overflow: hidden;
    @include media-breakpoint-down(md) {
      padding-bottom: 85px;
    }
    &__info {
      align-items: center;
      margin-top: 150px;
      @include media-breakpoint-down(md) {
        margin-top: 0;
      }
      @include media-breakpoint-down(xs) {
        overflow: hidden;
      }
      p {
        margin-bottom: 30px;
      }
    }
    .block-head .right {
      @include media-breakpoint-down(md) {
        transform: translateY(-162%);
      }
      @include media-breakpoint-down(sm) {
        position: absolute;
        bottom: -60px;
        left: 50%;
        transform: translateX(-50%);
        z-index: 1;
        a {
          background-color: #fa333c;
          color: #fff;
        }
      }
      @include media-breakpoint-down(xs) {
        width: calc(100% - 32px);
        a {
          width: 100%;
          height: 68px;
          text-align: center;
          font-size: 18px;
        }
      }
    }
    .image-2x {
      max-width: 222px;
      display: none;
      filter: grayscale(100%);
      transition: 0.5s;
      &:hover {
        filter: grayscale(0);
      }
      @media (-webkit-min-device-pixel-ratio: 2), (min-resolution: 192dpi) {
        display: block;
      }
    }
    .image {
      display: block;
      filter: grayscale(100%);
      transition: 0.5s;
      &:hover {
        filter: grayscale(0);
      }
      @media (-webkit-min-device-pixel-ratio: 2), (min-resolution: 192dpi) {
        display: none;
      }
    }
    .item {
      margin: 0 -16px;
      @include media-breakpoint-down(md) {
        margin: 0;
      }
      @media (max-width: 359px) {
        margin-left: -25px;
      }
      > div {
        padding: 0 16px;
      }
      &.bottom {
        margin-left: -144px;
        margin-top: -32px;
        @include media-breakpoint-down(md) {
          margin-left: -134px;
        }
        @include media-breakpoint-down(sm) {
          margin-bottom: 100px;
        }
        @media (max-width: 359px) {
          margin-left: -160px;
        }
      }
    }
    .scrolling__text {
      color: #f1f1f1 !important;
    }
  }
  &__instagram {
    background: $gray2;
    padding: 55px 0 120px;
    @include media-breakpoint-down(sm) {
      padding-bottom: 180px;
    }
    .container {
      position: relative;
      .rectangles {
        position: absolute;
        top: -133px;
        right: 35px;
      }
    }
    .insta_link {
      color: $white;
      font-size: 21px;
      span {
        margin-left: 15px;
      }
    }
    &__row {
      align-items: center;
      margin-top: 55px;
      p {
        max-width: 260px;
        display: flex;
        margin-left: auto;
        @include media-breakpoint-down(sm) {
          max-width: none;
        }
      }
    }
    &__images {
      margin: 0 -12px;
      &__image {
        padding: 0 12px;
        margin-bottom: 24px;
        @include media-breakpoint-down(xs) {
          max-height: 328px;
          overflow: hidden;
        }
        .img {
          max-width: 100%;
          object-fit: cover;
          min-height: 100%;
          transition: 0.5s;
        }
      }
    }
    .block-head .right {
      @include media-breakpoint-down(sm) {
        position: absolute;
        bottom: -92px;
        left: 50%;
        transform: translateX(-50%);
        z-index: 1;
        a {
          background-color: #fa333c;
          color: #fff;
        }
      }
      @include media-breakpoint-down(xs) {
        width: calc(100% - 32px);
        a {
          width: 100%;
          height: 68px;
          text-align: center;
          font-size: 18px;
        }
      }
    }
  }
}
.top-hero {
  position: absolute;
  z-index: 5;
  top: 150px;
  bottom: 0;
  left: 0;
  right: 0;
  @media (max-height: 800px) {
    top: 120px;
  }
  h1 {
    color: $white;
    font-size: 70px;
    letter-spacing: 0.7px;
    line-height: 162px;
    text-transform: uppercase;
    max-width: 100%;
    width: 100%;
    margin-left: 120px;
    @include poppins-black;
    span {
      @include poppins-extrabold;
      font-size: 100px;
      @media (max-width: 991px) {
        font-size: 65px;
        line-height: 150px;
      }
      @media (max-width: 767px) {
        font-size: 42px;
        line-height: 0;
      }
    }
    @media (max-height: 1200px) {
      margin-left: 60px;
    }
    @media (max-height: 991px) {
      font-size: 55px;
      line-height: 150px;
    }
    @media (max-height: 800px) {
      line-height: 120px;
      padding: 5px 30px 20px 80px;
    }
    @media (max-width: 767px) {
      font-size: 36px;
      line-height: 135px;
    }
    .typed-cursor {
      color: $red;
      font-size: 100px;
      font-weight: 900;
      letter-spacing: 1px;
      @media (max-width: 991px) {
        font-size: 65px;
      }
      @media (max-width: 767px) {
        font-size: 42px;
      }
    }
  }
  &__subtitle {
    position: absolute;
    top: -30px;
    p {
      margin-bottom: 0;
      color: rgba(255, 255, 255, 0.65);
      font-size: 14px;
      @include dm-sans-bold;
    }
  }
  .container {
    z-index: 2;
    position: relative;
  }
  .social {
    position: relative;
    @media (max-width: 991px) {
      margin-top: 45px;
    }
    li {
      padding: 25px;
      background-color: rgba(255, 255, 255, 0.24);
      &:first-child {
        margin-left: 0;
      }
      @media (max-width: 767px) {
        padding: 18px;
      }
      a {
        width: 50px;
        height: 50px;
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 100%;
        overflow: hidden;
        transition: 0.3s;
        img {
          filter: brightness(0) invert(0);
          transition: 0.3s;
        }
        &:hover {
          img {
            filter: brightness(0) invert(1);
          }
        }
      }
      &:last-child {
        margin-right: 0;
      }
    }
  }
  &__main-title {
    max-width: 1140px;
    width: 100%;
    background-color: rgba(255, 255, 255, 0.24);
    min-height: 370px;
    @media (max-width: 991px) {
      min-height: 325px;
    }
    @media (max-width: 800px) {
      min-height: 205px;
    }
    @media (max-height: 800px) {
      min-height: 280px;
    }
  }
  &__bottom {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin-top: 100px;
    @media (max-width: 991px) {
      flex-direction: column;
      margin-top: 65px;
    }
  }
  &__projects {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
  }
  &__last {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    background-color: rgba(255, 255, 255, 0.24);
    transition: 0.5s;
    a {
      padding: 30px 32px;
      @include media-breakpoint-down(xl) {
        padding: 25px 15px;
      }
    }
  }
}
.home-portfolio {
  z-index: 1;
  position: relative;
  background-color: #f1f1f1;
  padding-top: 40px;
  padding-bottom: 60px;
  .block-head .right {
    @include media-breakpoint-only(md) {
      transform: translateY(-165%);
    }
    @include media-breakpoint-down(sm) {
      position: absolute;
      bottom: -104px;
      left: 50%;
      transform: translateX(-50%);
      z-index: 1;
      a {
        background-color: #fa333c;
        color: #fff;
      }
    }
    @include media-breakpoint-down(xs) {
      width: calc(100% - 32px);
      a {
        width: 100%;
        height: 68px;
        text-align: center;
        font-size: 18px;
      }
    }
  }
  &__container {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-start;
    margin-top: 90px;
    @include media-breakpoint-down(md) {
      flex-direction: column;
      margin-top: 35px;
    }
  }
  &__left {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    margin-right: 20px;
    position: relative;
    perspective: 2000px;
    animation-name: fadeInUp;
    visibility: visible;
    @include media-breakpoint-down(md) {
      margin-right: 0;
      margin-bottom: 30px;
      width: 100%;
    }
  }
  &__right {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    margin-left: 20px;
    position: relative;
    perspective: 2500px;
    visibility: visible;
    @include media-breakpoint-down(md) {
      margin-left: 0;
      margin-bottom: 30px;
      width: 100%;
    }
  }
  &__tile {
    width: 100%;
    border-radius: 10px;
    margin-bottom: 65px;
    overflow: hidden;
    position: relative;
    will-change: transition, transform;
    transition: all 0.3s, background 0.9s, transform 0.1s;
    backface-visibility: hidden;
    transform: rotateX(0deg) rotateY(0deg);
    @include flex-center;
    img {
      max-width: 100%;
      transition: 0.4s;
      transform: scale(1);
      @include media-breakpoint-down(md) {
        width: 100%;
      }
    }
    &:hover {
      img {
        transform: scale(1.03);
      }
    }
  }
}



