.capability-tile {
  padding: 31px 40px 49px;
  border: 2px solid #d6d6d6;
  max-width: 453px;
  transition: 0.2s;
  @include media-breakpoint-down(lg) {
    max-width: 412px;
  }
  @include media-breakpoint-down(md) {
    max-width: 340px;
    padding: 23px 23px 25px;
  }
  &__img {
    margin-right: 16px;
    border-radius: 50%;
    width: 34px;
    height: 34px;
    img {
      width: 100%;
      height: 100%;
    }
  }
  &__title {
    h5 {
      font-size: 24px;
      &::first-letter {
        color: #fa333c;
      }
    }
  }
  &__description {
    font-size: 18px;
    line-height: 28px;
    color: #404040;
    margin-top: 30px;
    margin-bottom: 45px;
  }
  &__projects {
    p {
      font-size: 18px;
      margin-bottom: 18px;
    }
    a {
      color: #0d0d0d;
      font-size: 18px;
      font-weight: 500;
      transition: 0.3s;
      &:hover {
        color: #fa333c;
        text-decoration: underline;
      }
    }
  }
  &:hover {
    border-color: rgba(224, 32, 32, 0.72);
  }
}



