.project {
  padding: 164px 0 65px;
  background-color: #d8d8d8;
  .container {
    display: flex;
  }
  h2 {
    font-size: 36px;
    font-weight: 500;
    line-height: 52px;
  }
  &__info {
    max-width: 343px;
    h4 {
      font-size: 20px;
      font-weight: 500;
      margin-bottom: 19px;
    }
    p {
      font-size: 18px;
      margin-bottom: 44px;
    }
    .back {
      color: $body-color;
      display: inline-flex;
      margin-top: 33px;
      @extend .flex-center;
      img {
        margin-right: 8px;
      }
    }
  }
  &__type-switch {
    margin: 16px 0 48px;
    button {
      background: none;
      border: none;
      outline: none;
    }
    #desktop,
    #mobile {
      opacity: 0.43;
      &.active {
        opacity: 1;
      }
    }
    #desktop {
      margin-right: 16px;
    }
  }
  &__we-made {
    margin-bottom: 24px;
    span {
      border: 1px solid #fa333c;
      border-radius: 24px;
      padding: 7px 16px;
      font-size: 14px;
      font-weight: 500;
      &:not(:last-child) {
        margin-right: 16px;
      }
      &.muted {
        border-color: #0d0d0d;
        opacity: 0.5;
      }
    }
  }
  &__links {
    margin-bottom: 76px;
    a {
      font-size: 16px;
      font-weight: 500;
      color: $body-color;
      transition: 0.2s;
      &:not(:last-child) {
        margin-right: 46px;
      }
      &:hover {
        color: #fa333c;
      }
    }
  }
  &__frame {
    width: 100%;
    max-width: 662px;
    max-height: 448px;
    padding: 33px 19px 28px;
    background-size: contain;
    background-repeat: no-repeat;
    position: relative;
    margin-top: 48px;
    iframe {
      width: 200%;
      height: 200%;
      transform: scale(0.5);
      transform-origin: 0 0;
    }
    img {
      position: absolute;
      bottom: -17px;
      left: -71px;
      width: calc(100% + 142px);
    }
    &.mobile {
      max-width: 249px;
      max-height: 497px;
      display: none;
      padding-bottom: 18px;
      iframe {
        border-bottom-left-radius: 35px;
        border-bottom-right-radius: 35px;
      }
    }
  }
  &__ref {
    a {
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
    }
    &__left {
      left: 70px;
    }
    &__right {
      right: 70px;
    }
  }
}



