.hire-us {
  padding: 150px 0;
  @include media-breakpoint-down(md) {
    padding: 85px 0;
  }
  @include media-breakpoint-down(xs) {
    padding: 40px 0;
  }
  @media (max-height: 800px) {
    padding: 85px 0;
  }
  @media (max-height: 700px) {
    padding: 40px 0;
  }
  &__block {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    flex-wrap: wrap;
    background-color: rgba(255, 255, 255, 0.15);
    padding: 48px 48px 68px 48px;
    @include media-breakpoint-down(xs) {
      padding: 35px 20px 30px 20px;
    }
    h2 {
      color: #fff;
      margin-bottom: 52px;
      width: 100%;
      @include poppins-regular;
      font-weight: 700;
      @include media-breakpoint-down(sm) {
        margin-bottom: 30px;
      }
      @include media-breakpoint-down(xs) {
        margin-bottom: 25px;
      }
    }
    p {
      color: #bebbbb;
      font-size: 21px;
      line-height: 32px;
      margin-bottom: 0;
      max-width: 520px;
      @include dm-sans-medium;
    }
    &__right {
      .btn {
        margin-top: -25px;
        @include media-breakpoint-down(md) {
          margin-top: 25px;
        }
      }
      a {
        min-width: 304px;
      }
    }
  }
}



