.numbers {
  overflow: hidden;
  background-color: #f7f7f7;
  padding-top: 120px;
  padding-bottom: 45px;
  @include media-breakpoint-down(sm) {
    padding-top: 40px;
    padding-bottom: 0;
  }
  @media (max-width: 450px) {
    border-top: 2px solid;
    padding-top: 80px;
  }
  .container {
    position: relative;
    & * {
      z-index: 1;
    }
    &:before {
      content: url("images/portfolio/bg.svg");
      position: absolute;
      z-index: 0;
      right: 15px;
      top: -204px;
      overflow: hidden;
      max-height: 412px;
    }
  }
  &__wrapper {
    flex-wrap: wrap;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }
  &__left {
    margin-right: 32px;
    @media (max-width: 450px) {
      display: none;
      visibility: hidden;
    }
  }
  &__right {
    @media (max-width: 450px) {
      margin: auto;
    }
    p {
      @media (max-width: 450px) {
        text-align: center;
      }
    }
  }
  &__item {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    width: 50%;
    transition: 0.3s;
    margin-bottom: 105px;
    @include media-breakpoint-down(md) {
      width: 100%;
    }
    @media (max-width: 450px) {
      margin-bottom: 60px;
    }
  }
  &__number {
    transition: 0.3s;
    color: #fa333c;
    font-size: 40px;
    @include dm-sans-bold;
  }
  &__descr {
    color: #0d0d0d;
    font-size: 21px;
    transition: 0.3s;
    @include dm-sans-reg;
  }
}
.we-do__hero__img {
  min-height: 600px;
}
.our-team {
  padding-top: 85px;
  padding-bottom: 180px;
  background-color: #f1f1f1;
  margin-bottom: 800px;
  @include media-breakpoint-down(lg) {
    padding-bottom: 0;
  }
  @include media-breakpoint-down(sm) {
    padding-top: 30px;
  }
  .left-position-text {
    span {
      @include media-breakpoint-down(sm) {
        padding-bottom: 20px;
      }
    }
  }
  &__wrapper {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    flex-wrap: wrap;
    @media (max-width: 575px) {
      flex-direction: column;
      align-items: center;
    }
  }
  &__item {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    width: 214px;
    margin-right: 94px;
    margin-bottom: 108px;
    &:nth-child(4n) {
      margin-right: 0;
    }
    @media (max-width: 1270px) {
      margin-right: 206px;
      &:nth-child(4n) {
        margin-right: 206px;
      }
      &:nth-child(3n) {
        margin-right: 0;
      }
    }
    @media (max-width: 1200px) {
      margin-right: 96px;
      &:nth-child(4n) {
        margin-right: 96px;
      }
      &:nth-child(3n) {
        margin-right: 0;
      }
    }
    @media (max-width: 991px) {
      margin-right: 24px;
      &:nth-child(4n) {
        margin-right: 24px;
      }
      &:nth-child(3n) {
        margin-right: 0;
      }
    }
    @media (max-width: 767px) {
      margin-right: auto;
      &:nth-child(4n) {
        margin-right: auto;
      }
      &:nth-child(3n) {
        margin-right: auto;
      }
      &:nth-child(2n) {
        margin-right: 0;
      }
    }
    @media (max-width: 575px) {
      margin-right: auto;
      margin-left: auto;
      &:nth-child(4n) {
        margin-right: auto;
        margin-left: auto;
      }
      &:nth-child(3n) {
        margin-right: auto;
        margin-left: auto;
      }
      &:nth-child(2n) {
        margin-right: auto;
        margin-left: auto;
      }
    }
    @media (max-width: 450px) {
      margin-bottom: 60px;
    }
    &:last-child {
      margin-right: auto;
    }
    h3 {
      color: #0d0d0d;
      font-size: 18px;
      margin-bottom: 9px;
      margin-top: 16px;
      text-align: center;
      @include dm-sans-medium;
    }
    h3::first-letter {
      color: #fa333c;
    }
    p {
      margin-bottom: 0;
      opacity: 0.72;
      color: #0d0d0d;
      font-size: 14px;
      text-align: center;
      @include dm-sans-reg;
    }
  }
  &__img {
    width: 100%;
    height: 260px;
    background-position: center top;
    background-size: cover;
  }
}



